*{
    box-sizing: border-box;
    margin:0;
    padding: 0;
}

body{
    line-height: 1.4;
}

a{
    color:#333;
    text-decoration: none;
}

.btn{
    display: inline-block;
    border: none;
    background:#555;
    color:#fff;
    padding: 7px 20px;
    cursor:pointer;
    
}

.btn:hover{
    background: #666;
}